import React from 'react'
import {connect} from 'react-redux'
import {filterChanged, loadArticles} from '../redux/actions';

class App extends React.Component {

    constructor(props) {
        super(props);
    }

    render() {

        const {categories, countries, languages} = this.props.filtersData;

        const categoriesChoices = categories.map((cat, index) => {
            return <label className={'' || 'selected'} key={index}>
                <input type="radio"
                       name="category"
                       value={cat}
                       onChange={this.props.choiceSelected()}
                       checked=""/>
                {cat}
            </label>
        });

        const countriesChoices = countries.map((country, index) => {
            return <label className={'' || 'selected'} key={index}>
                <input type="radio"
                       name="country"
                       value={country}
                       onChange={this.props.choiceSelected()}
                       checked=""/>
                {country}
            </label>
        });

        const langChoices = languages.map((lang, index) => {
            return <label className={'' || 'selected'} key={index}>
                <input type="radio"
                       name="language"
                       value={lang}
                       onChange={this.props.choiceSelected()}
                       checked=""/>
                {lang}
            </label>
        });

        return <div id="loggedInState">
            <div id="topMenu">
                <fieldset id="categoriesChoices">
                    {categoriesChoices}
                </fieldset>
                <fieldset id="countriesChoices">
                    {countriesChoices}
                </fieldset>
                <fieldset id="langChoices">
                    {langChoices}
                </fieldset>
            </div>
        </div>

    }

}


function mapDispatchToProps(dispatch) {
    return {
        choiceSelected(param, value) {
            //dispatch(filterChanged(param, value));
        },
        sourceSelected(sourceId) {
            dispatch(loadArticles(sourceId));
        }
    };
}

function mapStateToProps(state) {
    return {
        filtersData: state.filtersData
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);


